html {
    font-size: 62.5%;
    min-width: 320px;

    @media screen and (max-width: 1550px) {
        font-size: 60%;
    }

    @media screen and (max-width: 1400px) {
        font-size: 58%;
    }

    @media screen and (max-width: 1300px) {
        font-size: 54%;
    }

    @media screen and (max-width: 1200px) {
        font-size: 50%;
    }
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    color: inherit;
    text-decoration: none;
}

body {
    position: relative;
    font-family: var(--font-1);
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 1.5;
    color: black;
    background: #fff;
}

body::after {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    display: none;
    width: 1px;
    height: 100vh;
    background: green;
    z-index: 1000;
}

img {
    max-width: 100%;
    height: auto;
}

.wrapper {
    position: relative;
    margin: 0 auto;
    max-width: 1440px;
}

li {
    list-style-type: none;
}

.container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1430px;
    width: 100%;
}

.container-big {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1750px;
    width: 100%;
}

.title {
    font-family: var(--font-2);
    font-size: 2.8rem;
    font-weight: 600;
    line-height: 1.3;
    letter-spacing: -0.056rem;
    text-transform: lowercase;

    &--small {
        font-size: 2.4rem;
        font-weight: bold;
    }
}

.text {
    line-height: 1.5;
    letter-spacing: 0.036rem;
}

.label {
    font-family: var(--font-2);
    font-size: 2.4rem;
    font-weight: 700;
    letter-spacing: 0.2rem;
    text-transform: uppercase;
}

.text p {
    margin-bottom: 1rem;
    line-height: 1.5;
    text-align: justify;
    letter-spacing: 0.036rem;
}

.text p:last-of-type {
    margin-bottom: 0;
}

.text a {
    position: relative;
}

.text a:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 5px;
    background: var(--accent);
    opacity: 1;
    transition: all .4s;
}

.text a:hover:after {
    opacity: 0;
}

.subtitle {
    font-size: 3.2rem;
    font-weight: 900;
    line-height: 1.3;
    color: var(--text-dark);
}